<template>
  <InfoPage align="center" white="true">
    <h1>
      WHAT COULD WE IMPROVE?<br>
    </h1>

    <textarea v-model="feedbackMsg" placeholder="WRITE YOUR FEEDBACK"></textarea>

    <a class="btn main gold" @click="submitFeedback">SHARE YOU OPINION</a>
  </InfoPage>
</template>

<script>
  //TODO: disable Submit button on start, enable after selecting rating.
  import Messages from "../../js/messages";
  import {Storage} from "../../js/storage";

  import InfoPage from "../components/InfoPage.vue";
  import {ROUTE_THANK_YOU} from "../router";

  export default {
    data() {
      return {
        feedbackMsg: ''
      }
    },
    methods: {
      submitFeedback() {
        Messages.toApi().submitFeedback(this.feedbackMsg);
        Storage.pages.setPage(null);

        this.$router.push({path: ROUTE_THANK_YOU});
      }
    },
    components: {
      InfoPage
    }
  }
</script>

