export const PLATFORM_CHROME = 'chrome';
export const PLATFORM_FIREFOX = 'firefox';
export const PLATFORM_OPERA = 'opera';
export const PLATFORM_UNKNOWN = 'unknown';


export const PlatformUtils = {
  apiBase() {
    return this.isFirefox() ? window.browser : window.chrome;
  },
  isChrome() {
    return navigator.userAgent.indexOf("Chrome") !== -1;
  },

  isFirefox() {
    return navigator.userAgent.indexOf("Firefox") !== -1;
  },

  isOpera() {
    return navigator.userAgent.indexOf(" OPR/") !== -1;
  },

  getPlatform(def=PLATFORM_UNKNOWN) {
    if (this.isOpera()) {
      return PLATFORM_OPERA;
    }
    if (this.isChrome()) {
      return PLATFORM_CHROME;
    }
    if (this.isFirefox()) {
      return PLATFORM_FIREFOX;
    }
    return def;
  }
};