import axios from 'axios';

import {addUniqueIdParam} from "./urls";

const URL_API_HOST = process.env.API_HOST;
const URL_API_SUBMIT_RATING = URL_API_HOST + '/rating.php';

const URL_API_ACTION_HTTP ='http://api.tuxler.com/ext_api.php?action=';
const URL_API_ACTION_HTTPS ='https://api.tuxler.com/ext_api.php?action=';

const addErrorHandler = async (func, params = []) => {
  try {
    return await func(...params);
  } catch (err) {
    console.error(err);
    console.error(err.response);
    console.error(err.response.status);

    return {success: false, msg: err.message};
  }
};

export const STATUS_WRONG_USERNAME_OR_PASSWORD = "WRONG_USERNAME_OR_PASSWORD";
export const STATUS_ERROR = "ERROR";
export const STATUS_PREMIUM_EXPIRED = "PREMIUM_EXPIRED";
export const STATUS_OK = "OK";


const ACTION_LOGIN = 'LOGIN';
const ACTION_STARTUP_DATA = 'STARTUP_DATA';
const ACTION_KEEP_ALIVE = 'KEEP_ALIVE';
const ACTION_GET_NEW_SERVER = 'GET_NEW_SERVER';

export const BackendApi = {
  async _doApiCall(action, params, https=true) {
    const host = https ? URL_API_ACTION_HTTPS : URL_API_ACTION_HTTP;
    const url = host + action + '&' + Object.entries(params).map(([param, value]) => param + '=' + encodeURIComponent(value)).join('&');

    // console.log('~_doApiCall~ url: ', url);

    try {
      const response = await axios.get(addUniqueIdParam(url));

      return response.data;
    } catch (e) {
      if (https) {
        return await this._doApiCall(action, params, false);
      }

      return {status: STATUS_ERROR, description: 'Unknown API error'};
    }
  },

  async startupData(username, password) {
    return await this._doApiCall(ACTION_STARTUP_DATA, {username, password});
  },

  async logIn(username, password) {
    return await this._doApiCall(ACTION_LOGIN, {username, password});
  },

  async keepAlive(username, password) {
    return await this._doApiCall(ACTION_KEEP_ALIVE, {username, password});
  },

  async getNewServer(username, password, country_code, city_name) {
    const params = {username, password};
    if (country_code) {
      params.country_code = country_code;
    }

    if (city_name) {
      params.city_name = city_name;
    }

    return await this._doApiCall(ACTION_GET_NEW_SERVER, params);
  },

  //TODO: handle error on rating submission
  async submitRating(rating, email) {
    console.log('BackendApi::submitRating()');

    return await addErrorHandler(async () => {
      // $.ajax({
      //   type: "POST",
      //   timeout: 5000,
      //   cache: false,
      //   url: "https://tuxler.com/rating.php",
      //   contentType: 'application/x-www-form-urlencoded',
      //   dataType: 'text',
      //   data: {type:'star', number:''+star_number, email:email},
      //   error: function(jqXHR, exception) {},
      //   success: function() {}
      // });

      const data = {type: 'star', number: String(rating), email: email};
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }
      return (await axios.post(URL_API_SUBMIT_RATING, data, config)).data;
    });
  },

async submitFeedback(feedback, email) {
    console.log('BackendApi::submitFeedback()');

    return await addErrorHandler(async () => {
      // $.ajax({
      //   type: "POST",
      //   timeout: 5000,
      //   cache: false,
      //   url: "https://tuxler.com/rating.php",
      //   contentType: 'application/x-www-form-urlencoded',
      //   dataType: 'text',
      //   data: {type:'feedback', email:email, feedback:feedback},
      //   error: function(jqXHR, exception) {},
      //   success: function() {}
      // });

      const data = {type: 'feedback', feedback, email};
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }
      return (await axios.post(URL_API_SUBMIT_RATING, data, config)).data;
    });
  }
};