<template>
  <div class="flex vertical-center space-beetween">
    <div class="protect-status" :class="classProtected">{{labelProtected}}</div>
    <Toggle :value="protected" @input="toggleProtection"></Toggle>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import Messages from "../../js/messages";
  import {Storage} from "../../js/storage";

  import {NS_SYSTEM} from "../store/system";

  import Toggle from '../components/Toggle.vue';
  import {ROUTE_DASHBOARD, ROUTE_PROTECTED} from "../router";
  import {STATUS_PREMIUM_EXPIRED} from "../../js/api";
  import {LoginMixin} from "../pages/mixins";

  export default {
    mixins: [LoginMixin],

    computed: {
      ...mapGetters(NS_SYSTEM, ['protected']),

      classProtected() {
        return this.protected ? 'protected' : 'unprotected';
      },
      labelProtected() {
        return this.protected ? 'protected' : 'unprotected';
      }
    },
    async mounted() {
      Storage.app.onConnectedStart((connected) => {
        console.log('---------------CONNECTED---------------')

        this.setProtected(connected);
        this._routePage();
      });
    },
    beforeDestroy() {
      Storage.app.onConnectedStop();
    },
    methods: {
      ...mapActions(['setLocation']),
      ...mapActions(NS_SYSTEM, ['setProtected']),

      async toggleProtection(enabled) {
        console.log('enabled: ', enabled)
        console.log('this.protected: ', this.protected)

        const location = await Messages.toBackground().toggleConnection(enabled);
        console.log('new location: ' + location);

        if (location === STATUS_PREMIUM_EXPIRED) {
          //TODO: We can handle this case in a better way - show message and then logout.
          this.logOut();
        } else if (location) {
          this.setLocation(location);
        }

        if (location !== STATUS_PREMIUM_EXPIRED) {
          this.setProtected(enabled);
          this._routePage();
        }
      },
      _routePage() {
        if (this.protected) {
          this.$router.push({path: ROUTE_PROTECTED});
        } else {
          this.$router.replace({path: ROUTE_DASHBOARD});
        }
      }
    },
    components: {
      Toggle
    }
  }
</script>

<style lang="scss" scoped>
  .protect-status {
    font-size: 13px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 4px 26px;
    border: 2px solid transparent;
    border-radius: 20px;
    display: inline-block;

    &.unprotected {
      color: #F20000;
      border-color: #F20000;
    }

    &.protected {
      color: #0BDCAD;
      border-color: #0BDCAD;
    }
  }
</style>

