<template>
  <InfoPage align="center" white="true" :icon="IMG_CROWN_ICON">
    <h1>
      YOUR PREMIUM<br>
      MEMBERSHIP INFORMATION
    </h1>

    <div class="inputs">
      <input type="text" :value="userEmail" disabled/>
    </div>

    <p v-if="info && expiresInDays" class="text-center">EXPIRATION DATE: <span class="text-black">{{expiresInDays}}</span></p>

    <a class="btn main gray mt-large" @click="logOut">LOG OUT</a>
  </InfoPage>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import {Storage} from "../../../js/storage";

  import InfoPage from "../../components/InfoPage.vue";

  import {IMG_CROWN_ICON} from "../../js/images";
  import {LoginMixin} from "../mixins";

  // TODO: do it
  // var message = chrome.i18n.getMessage("message_limit_free");
  // message = message.split("\\n").join("\n");


  export default {
    data() {
      return {
        info: null,
        expiresInDays: '',

        IMG_CROWN_ICON
      };
    },
    mixins: [LoginMixin],
    computed: {
      ...mapGetters(['user']),

      userEmail() {
        return this.user ? this.user.email : '';
      }
    },
    async mounted() {
      this.info = await Storage.user.getAccountInfo();
      console.log('this.info: ', this.info);

      if (this.info && this.info.expirationDate !== "-1") {
        const date = new Date(this.info.expirationDate);
        const timeDiff = Math.abs(date.getTime() - Date.now());
        let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24));

        if (diffDays <= 0) {
          diffDays = 1;
        }

        this.expiresInDays = diffDays + " " + chrome.i18n.getMessage("logged_in_day" + (diffDays > 1 ? "s" : ""));
      }
    },
    components: {
      InfoPage
    }
  }
</script>