<template>
    <div class="switcher">
        <input v-model="value" type="checkbox" class="checkbox" @change="onChange">
        <div class="knobs"></div>
        <div class="layer"></div>
    </div>
</template>

<script>
    export default {
        methods: {
            onChange(e) {
                this.$emit('input', e.target.checked);
            }
        },
        props: {
            value: Boolean
        }
    }
</script>

