import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import DashboardPage from '../pages/DashboardPage.vue';
import FeedbackPage from '../pages/FeedbackPage.vue';
import SettingsPage from '../pages/SettingsPage.vue';
import ProtectedPage from "../pages/ProtectedPage.vue";
import RatingPage from "../pages/RatingPage.vue";
import ShareRatingPage from "../pages/ShareRatingPage.vue";
import ReconnectingPage from "../pages/ReconnectingPage.vue";
import ThankYouPage from "../pages/ThankYouPage.vue";
import WelcomePage from "../pages/WelcomePage.vue";

import PremiumVersionPage from "../pages/menu/PremiumVersionPage.vue";
import MembershipInformationPage from "../pages/menu/MembershipInformationPage.vue";
import AboutPage from "../pages/menu/AboutPage.vue";
import SupportPage from "../pages/menu/SupportPage.vue";
import ResellerProgramPage from "../pages/menu/ResellerProgramPage.vue";
import UserGuidePage from "../pages/menu/UserGuidePage.vue";
import UninstallGuidePage from "../pages/menu/UninstallGuidePage.vue";
import LimitFreePage from "../pages/LimitFreePage.vue";
import LimitPremiumPage from "../pages/LimitPremiumPage.vue";
import LimitMultiLoginPage from "../pages/LimitMultiLoginPage.vue";
import LimitDailyLoginPage from "../pages/LimitDailyLoginPage.vue";
import FfPrivatePermission from "../pages/FfPrivatePermission.vue";
import AppisRequiredPage from "../pages/AppisRequiredPage.vue";

import MessagePage from "../pages/MessagePage.vue";

export const ROUTE_DASHBOARD = '/dashboard';
export const ROUTE_FEEDBACK = '/feedback';
export const ROUTE_RECONNECTING = '/reconnecting';
export const ROUTE_PROTECTED = '/protected';
export const ROUTE_SETTINGS = '/settings';
export const ROUTE_THANK_YOU = '/thank-you';
export const ROUTE_WELCOME = '/welcome';
export const ROUTE_RATING = '/rating';
export const ROUTE_INFO_MESSAGE = '/info-message';
export const ROUTE_LIMIT_FREE = '/limit-free';
export const ROUTE_LIMIT_PREMIUM = '/limit-premium';
export const ROUTE_LIMIT_MULTI_LOGIN = '/limit-multi-login';
export const ROUTE_LIMIT_DAILY_LOGIN = '/limit-daily-login';
export const ROUTE_FF_PRIVATE_PERMISSION = '/ff-private-permission';
export const ROUTE_APP_IS_REQUIRED = '/app-is-required';

export const ROUTE_SHARE_RATING = '/share-rating';

export const ROUTE_MENU_ABOUT = '/menu/about';
export const ROUTE_MENU_SUPPORT = '/menu/support';
export const ROUTE_MENU_RESELLER_PROGRAM = '/menu/reseller-program';
export const ROUTE_MENU_USER_GUIDE = '/menu/user-guide';
export const ROUTE_MENU_UNINSTALL_GUIDE = '/menu/uninstall-guide';
export const ROUTE_MENU_PREMIUM_VERSION = '/menu/premium-version';
export const ROUTE_MENU_MEMBERSHIP_INFORMATION = '/menu/membership-information';

const nameFromRoute = route => route.split('/').pop();


const router = new VueRouter({
  routes: [
    {path: ROUTE_THANK_YOU, component: ThankYouPage},
    {path: ROUTE_WELCOME, component: WelcomePage},
    {path: ROUTE_DASHBOARD, component: DashboardPage},
    {path: ROUTE_FEEDBACK, component: FeedbackPage},
    {path: ROUTE_RECONNECTING, component: ReconnectingPage},
    {path: ROUTE_PROTECTED, component: ProtectedPage},
    {path: ROUTE_RATING, component: RatingPage},
    {path: ROUTE_LIMIT_FREE, component: LimitFreePage},
    {path: ROUTE_LIMIT_PREMIUM, component: LimitPremiumPage},
    {path: ROUTE_LIMIT_MULTI_LOGIN, component: LimitMultiLoginPage},
    {path: ROUTE_LIMIT_DAILY_LOGIN, component: LimitDailyLoginPage},
    {path: ROUTE_FF_PRIVATE_PERMISSION, component: FfPrivatePermission},
    {path: ROUTE_APP_IS_REQUIRED, component: AppisRequiredPage},
    {path: ROUTE_SHARE_RATING, component: ShareRatingPage},
    {path: ROUTE_SETTINGS, component: SettingsPage},
    {path: ROUTE_INFO_MESSAGE, component: MessagePage},

    {path: ROUTE_MENU_ABOUT, component: AboutPage, name: nameFromRoute(ROUTE_MENU_ABOUT), meta:{title: 'ABOUT TUXLER'}},
    {path: ROUTE_MENU_SUPPORT, component: SupportPage, name: nameFromRoute(ROUTE_MENU_SUPPORT), meta:{title: 'SUPPORT'}},
    {path: ROUTE_MENU_RESELLER_PROGRAM, component: ResellerProgramPage, name: nameFromRoute(ROUTE_MENU_RESELLER_PROGRAM), meta:{title: 'RESSELLER PROGRAM'}},
    {path: ROUTE_MENU_USER_GUIDE, component: UserGuidePage, name: nameFromRoute(ROUTE_MENU_USER_GUIDE), meta:{title: 'USER GUIDE'}},
    {path: ROUTE_MENU_UNINSTALL_GUIDE, component: UninstallGuidePage, name: nameFromRoute(ROUTE_MENU_UNINSTALL_GUIDE), meta:{title: 'UNINSTALL GUIDE'}},
    {path: ROUTE_MENU_PREMIUM_VERSION, component: PremiumVersionPage, name: nameFromRoute(ROUTE_MENU_PREMIUM_VERSION), meta:{title: 'FREE VERSION'}},
    {path: ROUTE_MENU_MEMBERSHIP_INFORMATION, component: MembershipInformationPage, name: nameFromRoute(ROUTE_MENU_MEMBERSHIP_INFORMATION), meta:{title: 'PREMIUM VERSION'}},
  ]
});

export default router;