import {Storage} from "../../js/storage";

export const NS_SETTINGS = 'SETTINGS';

export default {
  namespaced: true,
  state: {
    advancedAnalysing: Storage.settings.getAdvancedAnalysing(),
  },
  getters: {
    advancedAnalysing: state => state.advancedAnalysing,
  },
  actions: {
    setAdvancedAnalysing({commit}, val) {
      commit('SET_ADVANCED_ANALYSING', val);
    },
  },
  mutations: {
    SET_ADVANCED_ANALYSING(state, val) {
      state.advancedAnalysing = val;

      Storage.settings.setAdvancedAnalysing(val);
    }
  }
}