import {MSG_POPUP_SHOW_PAGE, MSG_POPUP_SHOW_MESSAGE} from "../../js/actions";
import {
  PAGE_RATING,
  PAGE_LIMIT_FREE,
  PAGE_LIMIT_PREMIUM,
  PAGE_LIMIT_MULTI_LOGIN,
  PAGE_LIMIT_DAILY_LOGIN,
  PAGE_FF_PRIVATE_PERMISSION,
} from "../../js/pages";

import {
  ROUTE_DASHBOARD,
  ROUTE_RATING,
  ROUTE_WELCOME,
  ROUTE_INFO_MESSAGE,
  ROUTE_LIMIT_FREE,
  ROUTE_LIMIT_PREMIUM,
  ROUTE_LIMIT_MULTI_LOGIN,
  ROUTE_PROTECTED,
  ROUTE_LIMIT_DAILY_LOGIN,
  ROUTE_FF_PRIVATE_PERMISSION
} from "../router";

import {Storage} from "../../js/storage";
import {mapActions} from "vuex";
import {NS_UI} from "../store/ui";
import {NS_SYSTEM} from "../store/system";
import Messages from "../../js/messages";

const PAGE_TO_ROUTE = {
  [PAGE_RATING]: ROUTE_RATING,
  [PAGE_LIMIT_FREE]: ROUTE_LIMIT_FREE,
  [PAGE_LIMIT_PREMIUM]: ROUTE_LIMIT_PREMIUM,
  [PAGE_LIMIT_MULTI_LOGIN]: ROUTE_LIMIT_MULTI_LOGIN,
  [PAGE_LIMIT_DAILY_LOGIN]: ROUTE_LIMIT_DAILY_LOGIN,
  [PAGE_FF_PRIVATE_PERMISSION]: ROUTE_FF_PRIVATE_PERMISSION
};

export const RouterMixin = {
  methods: {
    ...mapActions(NS_SYSTEM, ['setProtected']),

    async routeAfterLogin() {
      console.log('Storage.app.isInstalled(): ', Storage.app.isInstalled());
      console.log('Storage.pages.getPage(): ', Storage.pages.getPage());

      const page = Storage.pages.getPage();
      if (page) {
        Storage.pages.setPage(null);
        this.$router.push({path: PAGE_TO_ROUTE[page]});
      } else {
        const isProxySet = await Messages.toBackground().isProxySet();
        console.log('############### RouterMixin::isProxySet: ', isProxySet);

        if (isProxySet) {
          this.setProtected(true);
          this.$router.push({path: ROUTE_PROTECTED});
        } else {
          this.$router.push({path: ROUTE_DASHBOARD});
        }
      }
    }
  }
};

export const PagesRoutingMixin = {
  mounted() {
    chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
      switch (message.action) {
        case MSG_POPUP_SHOW_PAGE: {
          this.$router.push({path: PAGE_TO_ROUTE[message.page]});
          break;
        }
        case MSG_POPUP_SHOW_MESSAGE: {
          this.setMessage(message.msg);
          this.$router.push({path: ROUTE_INFO_MESSAGE});
          break;
        }
      }
    });
  },
  methods: {
    ...mapActions(NS_UI, ['setMessage'])
  }
};