import {
  MSG_DOWNLOAD_APP,
  MSG_LOCATION_SELECTED,
  MSG_SET_NEXT_NEARBY,

  MSG_APP_GET_ACCOUNT_INFO,

  MSG_STARTUP_DATA,
  MSG_API_LOG_IN,
  MSG_API_SUBMIT_RATING,
  MSG_API_SUBMIT_FEEDBACK,

  MSG_POPUP_UPDATE_LAST_TIME_CHANGED,
  MSG_POPUP_UPDATE_LAST_LOCATION,
  MSG_POPUP_SHOW_PAGE,
  MSG_POPUP_SEND_ACCOUNT_INFO,
  MSG_POPUP_SHOW_MESSAGE,

  MSG_GET_FLAG_ICON,
  MSG_IS_PROXY_SET,
  MSG_TOGGLE_CONNECTION,

  MSG_GET_USER,
  MSG_LOG_IN,
  MSG_LOG_OUT,
} from './actions';
import {PopupUtils} from "./popup";

const runtimeMessagePromise = data => new Promise((resolve, reject) => {
  chrome.runtime.sendMessage(data, response => {
    if (chrome.runtime.lastError) {
      console.error('chrome.runtime.lastError: ', chrome.runtime.lastError);
      console.error('message data: ', data);
      reject(chrome.runtime.lastError);
    } else {
      resolve(response);
    }
  });
});

const Messages = new class {
  constructor() {
    this._eventHandlers = [];

    chrome.runtime.onMessage.addListener((data, sender, sendResponse) => {
      this._router(data, sender, sendResponse);
    });
  }

  on(handlers) {
    this._eventHandlers.push(handlers);
  }

  _router(data, sender, sendResponse) {
    this._eventHandlers.forEach(handlersBlock => {
      for (let i = 0; i < handlersBlock.length; i += 2) {
        if (handlersBlock[i] === data.action) {
          handlersBlock[i + 1](data, sender, sendResponse);
        }
      }
    });
  }

  toApi() {
    return {
      startupData(username, passHash) {
        return runtimeMessagePromise({action: MSG_STARTUP_DATA, username, passHash});
      },
      logIn(username, passHash) {
        return runtimeMessagePromise({action: MSG_API_LOG_IN, username, passHash});
      },
      submitRating(rating) {
        return runtimeMessagePromise({action: MSG_API_SUBMIT_RATING, rating});
      },
      submitFeedback(msg) {
        return runtimeMessagePromise({action: MSG_API_SUBMIT_FEEDBACK, msg});
      },
    }
  }
  toApp() {
    return {
      getAccountInfo(email, pass) {
        return runtimeMessagePromise({action: MSG_APP_GET_ACCOUNT_INFO, args: [email, pass]});
      }
    }
  }

  toPopup() {
    return {
      updateLastTimeChanged(time) {
        if (PopupUtils.isPopupOpened()) {
          return runtimeMessagePromise({action: MSG_POPUP_UPDATE_LAST_TIME_CHANGED, time});
        }
      },
      updateLastLocation(location) {
        console.trace('~updateLastLocation~', JSON.stringify(location));
        if (PopupUtils.isPopupOpened()) {
          return runtimeMessagePromise({action: MSG_POPUP_UPDATE_LAST_LOCATION, location});
        }
      },
      sendAccountInfo(valid, premium) {
        if (PopupUtils.isPopupOpened()) {
          return runtimeMessagePromise({action: MSG_POPUP_SEND_ACCOUNT_INFO, valid});
        }
      },
      showPage(page) {
        if (PopupUtils.isPopupOpened()) {
          return runtimeMessagePromise({action: MSG_POPUP_SHOW_PAGE, page});
        }
      },
      showMessage(msg) {
        if (PopupUtils.isPopupOpened()) {
          return runtimeMessagePromise({action: MSG_POPUP_SHOW_MESSAGE, msg});
        }
      }
    }
  }

  toBackground() {
    return {
      downloadApp(url) {
        return runtimeMessagePromise({action: MSG_DOWNLOAD_APP, url});
      },
      locationSelected(countryISO, city) {
        console.trace("locationSelected", arguments)

        return runtimeMessagePromise({action: MSG_LOCATION_SELECTED, countryISO, city});
      },
      setNextNearby(countryISO, city) {
        return runtimeMessagePromise({action: MSG_SET_NEXT_NEARBY, countryISO, city});
      },
      toggleConnection(connected) {
        return runtimeMessagePromise({action: MSG_TOGGLE_CONNECTION, connected});
      },
      getUser() {
        return runtimeMessagePromise({action: MSG_GET_USER});
      },
      getFlagIcon(iso, medium=false) {
        return runtimeMessagePromise({action: MSG_GET_FLAG_ICON, medium, iso});
      },
      isProxySet() {
        return runtimeMessagePromise({action: MSG_IS_PROXY_SET});
      },
      logIn(email, password) {
        return runtimeMessagePromise({action: MSG_LOG_IN, email, password});
      }
    }
  }
};

export default Messages;