import Messages from "./messages";

import {Storage} from "./storage";
import {PopupUtils} from "./popup";
import {Notifications} from "./notifications";

export const PAGE_RATING = 'page_rating';
export const PAGE_INFO_MESSAGE = 'page_info_message';
export const PAGE_LIMIT_FREE = 'page_limit_free';
export const PAGE_LIMIT_PREMIUM = 'page_limit_premium';
export const PAGE_LIMIT_MULTI_LOGIN = 'page_limit_multi_login';
export const PAGE_LIMIT_DAILY_LOGIN = 'page_limit_daily_login';
export const PAGE_FF_PRIVATE_PERMISSION = 'page_ff_private_permission';

export const Pages = {
    showRatingPage() {
        this._show(PAGE_RATING);
    },
    showInfoMessage(msg) {
        if (PopupUtils.isPopupOpened()) {
            Messages.toPopup().showMessage(msg);
        } else {
            Notifications.showError(msg);
        }
    },
    showLimitFreePage() {
        this._show(PAGE_LIMIT_FREE);
    },
    showLimitPremiumPage() {
        this._show(PAGE_LIMIT_PREMIUM);
    },
    showLimitMultiLogin() {
        this._show(PAGE_LIMIT_MULTI_LOGIN);
    },
    showLimitDailyLogin() {
        this._show(PAGE_LIMIT_DAILY_LOGIN);
    },
    showPageFfPrivatePermission() {
        this._show(PAGE_FF_PRIVATE_PERMISSION);
    },
    _show(page) {
        if (PopupUtils.isPopupOpened()) {
            Messages.toPopup().showPage(page);
        } else {
            Storage.pages.setPage(page);
        }
    }
};