      <div class="inputs">
        <input type="text" v-model="email" placeholder="EMAIL ADDRESS" @keyup.enter="logIn"/>
        <input type="password" v-model="password" placeholder="PASSWORD" @keyup.enter="logIn"/>

        <div class="message">{{message}}</div>
      </div>
    </div>

    <div class="buttons">
      <a class="btn main gray" @click="logIn">LOG IN</a>
      <a class="btn main color" @click="upgradeToPremium">BUY PREMIUM</a>
    </div>

    <h1>FREE MEMBERSHIP</h1>
    <a class="btn main gold" @click="downloadApp">DOWNLOAD FREE APP</a>
  </Info>
</template>

<script>
  import Info from '../components/Info.vue';

  import Messages from "../../js/messages";
  import {SyncURL, AsyncURL} from "../../js/urls";

  import {LoginMixin} from "./mixins";
  import {TabUtils} from "../js/utils";

  export default {
    mixins: [LoginMixin],
    methods: {
      upgradeToPremium() {
        TabUtils.openTab(SyncURL.URL_UPGRADE_TO_PREMIUM)
      },
      async downloadApp() {
        const url = await AsyncURL.install();
        Messages.toBackground().downloadApp(url);
      }
    },
    components: {
      Info
    }
  }
</script>

