<script>
  import {mapGetters} from 'vuex';

  import {
    ROUTE_WELCOME,
  } from "../router";

  import {SyncURL} from "../../js/urls";
  import {TabUtils} from "../js/utils";

  export default {
    computed: {
      ...mapGetters(['premiumActive']),

      premiumStatusClass() {
        return  this.premiumActive ? 'premium' :' no-premium';
      },
      premiumStatusLabel() {
        return  this.premiumActive ? 'PREMIUM MEMBERSHIP' :' BUY PREMIUM';
      },
      hasPremiumStatusBar() {
        return ![ROUTE_WELCOME].includes(this.$route.path);
      }
    },
    methods: {
      buyPremium() {
        if (this.premiumActive) {
          return;
        }

        TabUtils.openTab(SyncURL.URL_UPGRADE_TO_PREMIUM);
      }
    }
  }
</script>

