<template>
  <div class="popup">
    <MenuSidebar></MenuSidebar>
    <Header></Header>
    <router-view class="container"></router-view>
    <Footer v-if="isFooterVisible"></Footer>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import {_} from './js/i18n';


  import MenuSidebar from './components/MenuSidebar.vue';
  import Header from './components/Header.vue';
  import Footer from './components/Footer.vue';
  import {NS_SUBSCRIPTION} from "./store/subsription";

  import {ROUTE_DASHBOARD, ROUTE_PROTECTED} from "./router";
  import {
    MSG_POPUP_UPDATE_LAST_LOCATION,
    MSG_POPUP_UPDATE_LAST_TIME_CHANGED
  } from "../js/actions";

  export default {
    data() {
      return {
        fbLoaded: false
      }
    },
    computed: {
      ...mapGetters(['user']),
      ...mapGetters(NS_SUBSCRIPTION, ['isLimitReached']),

      isFooterVisible() {
        return [ROUTE_DASHBOARD, ROUTE_PROTECTED].includes(this.$route.path);
      }
    },
    mounted() {
      chrome.runtime.onMessage.addListener(this._appMessageHandler);
    },
    beforeDestroy() {
      chrome.runtime.onMessage.removeListener(this._appMessageHandler);
    },
    methods: {
      ...mapActions(['setUser', 'setLocation']),

      _appMessageHandler(message, sender, sendResponse) {
        console.log('~Popup -> _appMessageHandler~: ', message);

        switch (message.action) {
          case MSG_POPUP_UPDATE_LAST_TIME_CHANGED: {
            console.log('message.time: ', message.time)

            break;
          }

          case MSG_POPUP_UPDATE_LAST_LOCATION: {
            console.log('message.location: ', message.location)

            this.setLocation(message.location);
            break;
          }
        }
      },
      _(key) {
        return _(key);
      }
    },
    components: {
      Header,
      Footer,
      MenuSidebar
    }
  }
</script>

