import Vue from 'vue';
import {mapGetters, mapActions} from 'vuex';

import {createConnectionPort} from "../../js/popup";

import store from '../store';
import router, {ROUTE_WELCOME} from '../router';

import {RouterMixin, PagesRoutingMixin} from "./mixins";

import Popup from '../Popup.vue';

import {Storage} from "../../js/storage";

/**
 * Port to check if popup opened or not
 */
createConnectionPort();

new Vue({
  el: '#appRoot',
  mixins: [RouterMixin, PagesRoutingMixin],
  template: '<Popup></Popup>',
  router,
  store,
  computed: {
    ...mapGetters(['user']),
  },
  async mounted() {
    const user = await Storage.user.getUser();
    console.log('popup: user: ', user);

    this.setUser(user);

    if (Storage.app.isInstalled()) {
      this.routeAfterLogin();
    } else {
      if (user) {
        this.routeAfterLogin();
      } else {
        router.push({path: ROUTE_WELCOME});
      }
    }
  },
  methods: {
    ...mapActions(['setUser', 'setPremiumActive'])
  },
  components: {
    Popup
  }
});