<template>
  <InfoPage align="center" white="true">
    <h1>
      THANK YOU FOR RATING TUXLER VPN.<br>
      PLEASE SHARE YOUR THOUGTS<br>
      ABOUT TUXLER WITH CHROME COMMUNITY
    </h1>

    <div>
      <img class="chrome-store" :src="IMG_CHROME_STORE"/>

      <a class="btn main gold mt-large" @click="shareFeedback">SHARE YOU OPINION</a>
    </div>
  </InfoPage>
</template>

<script>
  import {SyncURL} from "../../js/urls";
  import {PlatformUtils} from "../../js/platform";

  import {ROUTE_DASHBOARD} from "../router";
  import {IMG_CHROME_STORE} from "../js/images";

  import InfoPage from "../components/InfoPage.vue";
  import {TabUtils} from "../js/utils";

  export default {
    data() {
      return {
        IMG_CHROME_STORE
      };
    },
    computed: {
      storeURL() {
        //TODO: what about opera
        return PlatformUtils.isChrome() ? SyncURL.URL_EXT_HOME_CHROME : SyncURL.URL_EXT_HOME_FIREFOX;
      }
    },
    methods: {
      async shareFeedback() {
        await TabUtils.openTab(this.storeURL);

        this.$router.replace({path: ROUTE_DASHBOARD});
      }
    },
    components: {
      InfoPage
    }
  }
</script>

<style lang="scss" scoped>
  img.chrome-store{
    width: 178px;
    height: 148px;
    display: block;
    margin: 0 auto;
  }
</style>