export const ls = {
  get(name, def) {
    const obj = localStorage[name];
    if (!obj) {
      return def;
    }
    try {
      return JSON.parse(obj);
    } catch (e) {
      return def;
    }
  },

  set(name, val) {
    localStorage[name] = JSON.stringify(val);
  },

  remove(name) {
    delete localStorage[name];
  }
};