import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

import {Storage} from '../../js/storage';

import modSystem, {NS_SYSTEM} from "./system";
import modSettings, {NS_SETTINGS} from "./settings";
import modUI, {NS_UI} from "./ui";


const store = new Vuex.Store({
  modules: {
    [NS_SYSTEM]: modSystem,
    [NS_SETTINGS]: modSettings,
    [NS_UI]: modUI
  },
  state: {
    user: null,
    lastTimeChanged: '',
    premiumActive: false,
    location: Storage.user.getLocation() || {},
  },
  getters: {
    user: state => state.user,
    lastTimeChanged: state => state.lastTimeChanged,
    premiumActive: state => state.premiumActive,
    location: state => state.location,
  },
  actions: {
    setUser({commit}, user) {
      commit('SET_USER', user);
    },
    setPremiumActive({commit}, status) {
      commit('SET_PREMIUM_ACTIVE', status);
    },
    setLocation({commit}, location) {
      commit('SET_LOCATION', location);
    },
    setLastTimeChanged({commit}, time) {
      commit('SET_LAST_TIME_CHANGED', time);
    }
  },
  mutations: {
    SET_USER(state, user) {
      Storage.user.setUser(user).then(() => {
        state.user = user;
      });
    },
    SET_PREMIUM_ACTIVE(state, status) {
      state.premiumActive = status;
    },
    SET_LAST_TIME_CHANGED(state, time) {
      state.lastTimeChanged = time;
    },
    SET_LOCATION(state, location) {
      state.location = location;
    }
  }
});

