<template>
  <div class="menu-sidebar" :class="{'visible': menuVisible}">
    <div class="header">
      <h1>MENU</h1>
      <IconExit @click="setMenuVisible(false)"></IconExit>
    </div>
    <ul>
      <li @click="openRoute(ROUTE_MENU_SUPPORT)">
        <span>SUPPORT</span>
      </li>
      <li v-if="hasUser" @click="openRoute(ROUTE_MENU_MEMBERSHIP_INFORMATION)">
        <span>MEMBERSHIP INFORMATION</span>
      </li>
      <li v-else @click="openRoute(ROUTE_MENU_PREMIUM_VERSION)">
        <span>UPGRADE TO PREMIUM</span>
      </li>
      <li @click="openRoute(ROUTE_MENU_ABOUT)">
        <span>ABOUT TUXLER</span>
      </li>
      <li @click="openRoute(ROUTE_MENU_RESELLER_PROGRAM)">
        <span>RESELLER PROGRAM</span>
      </li>
      <li @click="openRoute(ROUTE_MENU_USER_GUIDE)">
        <span>USER GUIDE</span>
      </li>
      <li @click="openRoute(ROUTE_MENU_UNINSTALL_GUIDE)">
        <span>UNINSTALL GUIDE</span>
      </li>
      <li @click="openRoute(ROUTE_RATING)">
        <span>RATING</span>
      </li>
    </ul>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import IconExit from './icons/IconExit.vue';
  import {NS_UI} from "../store/ui";
  import {
    ROUTE_RATING,

    ROUTE_MENU_PREMIUM_VERSION,
    ROUTE_MENU_MEMBERSHIP_INFORMATION,
    ROUTE_MENU_ABOUT,
    ROUTE_MENU_SUPPORT,
    ROUTE_MENU_RESELLER_PROGRAM,
    ROUTE_MENU_USER_GUIDE,
    ROUTE_MENU_UNINSTALL_GUIDE
  } from "../router";

  export default {
    data() {
      return {
        ROUTE_RATING,

        ROUTE_MENU_PREMIUM_VERSION,
        ROUTE_MENU_MEMBERSHIP_INFORMATION,
        ROUTE_MENU_ABOUT,
        ROUTE_MENU_SUPPORT,
        ROUTE_MENU_RESELLER_PROGRAM,
        ROUTE_MENU_USER_GUIDE,
        ROUTE_MENU_UNINSTALL_GUIDE
      }
    },
    computed: {
      ...mapGetters(['user']),
      ...mapGetters(NS_UI, ['menuVisible']),

      hasUser() {
        return this.user && this.user.email;
      }
    },
    methods: {
      ...mapActions(NS_UI, ['setMenuVisible']),

      openRoute(path) {
        this.$router.push({path});

        this.setMenuVisible(false)
      }
    },
    components: {
      IconExit
    }
  }
</script>

