import {App} from "./app";
import {Storage} from "./storage";
import {PLATFORM_CHROME, PLATFORM_FIREFOX, PLATFORM_OPERA, PlatformUtils} from "./platform";
import {Pages} from "./pages";

window._socksProxyAddr = null;

export const Proxy = {
  set() {
    App.setProxy();

    window.is_proxy_set = true;

    App.getLocalProxyAddress((addr) => {
      console.log('App.getLocalProxyAddress: ', addr)

      this.setSocksProxy(addr);
    });
  },

  _setChromeProxy(addr) {
    chrome.proxy.settings.set({
      value: {
        mode: "fixed_servers",
        rules: {
          singleProxy: {
            scheme: "socks5",
            host: addr.proxy,
            port: addr.port
          },
          bypassList: [
            '<local>',
            '*tuxler.com',
            '*tuxlervpn.com',
            '*tuxlervpn.com:700',
            '*tuxlervpn.com:701',
            `*tuxler.com:${addr.port}`,
            '*tuxler.com:701',
            '*tuxler.com:700']
        }
      },
      scope: 'regular'
    }, () => {
      console.log('PROXY SET CALLBACK:', chrome.runtime.lastError);

      window.is_proxy_set = true;
    });
  },

  // reconnectSocksProxy() {
  //   console.log('~reconnectSocksProxy~');
  //
  //   const addr = window._socksProxyAddr;
  //     chrome.proxy.settings.clear({scope: 'regular'}, () => {
  //       this._setChromeProxy(addr);
  //       console.log('RECONNECTED');
  //     });
  // },

  async setSocksProxy(addr) {
    console.log('~setSocksProxy~', addr);
    window._socksProxyAddr = addr;

    const platform = PlatformUtils.getPlatform(PLATFORM_CHROME);
    console.log('platform: ', platform);

    if (platform === PLATFORM_CHROME || platform === PLATFORM_OPERA) {

      try {
        chrome.privacy.network.webRTCIPHandlingPolicy.set({
          value: chrome.privacy.IPHandlingPolicy.DISABLE_NON_PROXIED_UDP
        });
      } catch (exc) {
        console.log(exc);
      }

      this._setChromeProxy(addr);
    } else if (platform === PLATFORM_FIREFOX) {
      // TODO: callback/promise?
      try {
        console.log(' changing proxy....');
        const result = await browser.proxy.settings.set({
          value: {
            proxyType: "manual",
            socks: "" + addr.proxy + ":" + addr.port,
            socksVersion: 5,
            proxyDNS: true,
            passthrough: [
              'tuxlervpn.com',
              'www.tuxlervpn.com',
              '*tuxlervpn.com',
              '*tuxlervpn.com:700',
              'www.tuxlervpn.com:700',
              '*tuxlervpn.com:701',
              'www.tuxlervpn.com:701',
              `*tuxlervpn.com:${addr.port}`,

              'tuxler.com',
              'www.tuxler.com',
              '*tuxler.com',
              '*tuxler.com:701',
              'tuxler.com:701',
              'www.tuxler.com:701',
              '*tuxler.com:700',
              'tuxler.com:700',
              `*tuxler.com:${addr.port}`,
              'www.tuxler.com:700',
              'localhost',
              '127.0.0.1'
            ].join(',')
          }
        });
        console.log('@@@@@result: ', result);
        window.is_proxy_set = true;
      } catch (e) {
        console.log('********* Private mode is not allowed.');
        Pages.showPageFfPrivatePermission();
      }
    }
  },

  unset() {
    console.log('~Proxy::unset()~');

    const platform = PlatformUtils.getPlatform(PLATFORM_CHROME);

    if (Storage.app.isInstalled()) {
      App.unsetProxy();
    }

    window.is_proxy_set = false;
    window._socksProxyAddr = null;

    if (platform === PLATFORM_CHROME || platform === PLATFORM_OPERA) {
      chrome.proxy.settings.clear({scope: 'regular'}, () => {
      });

      try {
        chrome.privacy.network.webRTCIPHandlingPolicy.set({
          value: chrome.privacy.IPHandlingPolicy.DEFAULT
        });
      } catch (exc) {
        console.log(exc);
      }
    } else if (platform === PLATFORM_FIREFOX) {
      browser.proxy.settings.set({
        value: {
          proxyType: "system"
        }
      });
    }
  },
  isSet() {
    return window.is_proxy_set;
  }
};