import {mapActions} from 'vuex';
import sha1 from 'js-sha1';

import Messages from "../../js/messages";
import {
  MSG_POPUP_SEND_ACCOUNT_INFO,
  MSG_POPUP_UPDATE_LAST_TIME_CHANGED
} from "../../js/actions";
import {ROUTE_DASHBOARD, ROUTE_MENU_PREMIUM_VERSION} from "../router";
import {Storage} from "../../js/storage";
import {Proxy} from "../../js/proxy";
import {NS_SYSTEM} from "../store/system";

const STATUS_WRONG_USERNAME_OR_PASSWORD = "WRONG_USERNAME_OR_PASSWORD";
const STATUS_PREMIUM_EXPIRED = "PREMIUM_EXPIRED";
const STATUS_ERROR = "ERROR";
const STATUS_OK = "OK";

export const LoginMixin = {
  data() {
    return {
      email: '',
      password: '',
      message: '',
      // email: 'viktar@test.com',
      // password: 'testpass',
    }
  },
  mounted() {
    console.log('LoginMixin - mounted');
    chrome.runtime.onMessage.addListener(this._appMessageHandler);
  },
  beforeDestroy() {
    console.log('LoginMixin - beforeDestroy');
    chrome.runtime.onMessage.removeListener(this._appMessageHandler);
  },
  methods: {
    ...mapActions(['setUser', 'setLocation', 'setLastTimeChanged']),
    ...mapActions(NS_SYSTEM, ['setUseResidentialIPs']),

    logOut() {
      this.setUser(null);

      Messages.toBackground().logIn('', '');

      this.$router.replace({path: ROUTE_MENU_PREMIUM_VERSION});
    },

    _appMessageHandler(message, sender, sendResponse) {
      console.log('~_appMessageHandler~: ', message);

      switch (message.action) {
        case MSG_POPUP_SEND_ACCOUNT_INFO: {
          this._accountInfoHandler(message.valid);
          break;
        }

        case MSG_POPUP_UPDATE_LAST_TIME_CHANGED: {
          this.setLastTimeChanged(message.time);
          break;
        }
      }
    },
    _accountInfoHandler(valid) {
      if (valid) {
        Messages.toBackground().logIn(this.email, this.password);

        const hasUser = this.email.length > 0 && this.password.length > 0;
        console.log('_accountInfoHandler hasUser', hasUser);

        if (hasUser) {
          this.setUser({email: this.email, password: sha1(this.password)});

          this.$router.replace({path: ROUTE_DASHBOARD});
        }
      } else {
        //TODO: show message
        // $('.shake-body').effect("shake");
      }
    },

    async getStartupData() {
      console.log('~getStartupData~');

      const data = await Messages.toApi().startupData(this.email, sha1(this.password));
      console.log('data: ', data);

      if (data.status === STATUS_OK) {
        const hasUser = this.email.length > 0 && this.password.length > 0;
        console.log('_accountInfoHandler hasUser', hasUser);

        if (hasUser) {
          this.setUser({email: this.email, password: sha1(this.password)});
          Storage.user.setPremium(true);
          Storage.user.setApiModeServers(data.data.servers);

          chrome.storage.local.set({
            'freePremiumTime': parseInt(new Date(data.data.user.premium_expires_unix_time) - Date.now() / 1000),
            'isLimit': false
          });

          const loc = {
            city: data.data.user.city_name,
            country: data.data.user.country_name,
            region: data.data.user.region_name,
            countryiso: data.data.user.country_code
          };

          this.setLocation(loc);
          Storage.user.setLocation(loc);
          Storage.user.setDefaultLocation(loc);

          this.setUseResidentialIPs(false);

          this.$router.replace({path: ROUTE_DASHBOARD});
        }
      }
    },

    async logIn() {
      console.log('~logIn~ Storage.app.isInstalled(): ', Storage.app.isInstalled());
      this.message = '';

      if (Storage.app.isInstalled()) {
        Messages.toApp().getAccountInfo(this.email, sha1(this.password));
      } else {

        const result = await Messages.toApi().logIn(this.email, sha1(this.password));
        console.log('result: ', result);

        switch (result.status) {
          case STATUS_WRONG_USERNAME_OR_PASSWORD:
            this.message = 'Wrong username or password';
            break;

          case STATUS_PREMIUM_EXPIRED:
            this.message = 'Your premium is expired';
            break;

          case STATUS_ERROR:
            this.message = 'Unexpected error. Try again later.';
            break;

          case STATUS_OK:
            this.getStartupData();
            break;
        }
      }
    }
  }
};