<template>
  <InfoPage align="center" white="true" :icon="IMG_INFO_ICON">
    <h1 class="mt-large">ACTION REQUIRED</h1>

    <p class="mb-xl text-center">TuxlerVPN Chrome Application is required <br>
      to start TuxlerVPN Extension to provide<br>
      you with maximum security.<br>
      Click on the button below to safely<br>
      install TuxlerVPN<br>
      Application from our website.</p>

    <a class="btn main gold" @click="installApp">INSTALL TUXLERVPN APPLICATION</a>
  </InfoPage>
</template>

<script>
  import InfoPage from "../components/InfoPage.vue";

  import {IMG_INFO_ICON} from "../js/images";
  import {AsyncURL} from "../../js/urls";
  import Messages from "../../js/messages";
  import {PlatformUtils} from "../../js/platform";

  export default {
    data() {
      return {
        IMG_INFO_ICON
      };
    },
    methods: {
      async installApp() {
        const url = await AsyncURL.install();
        await Messages.toBackground().downloadApp(url);

        if (PlatformUtils.isFirefox()) {
          window.close();
        }
      }
    },
    components: {
      InfoPage
    }
  }
</script>


