<template>
  <div class="header">
    <PremiumBar></PremiumBar>

    <div v-if="isMenuVisible" class="menu-bar">
      <h1 v-if="title">{{title}}</h1>
      <img v-else class="logo" :src="IMG_LOGO"/>

      <img class="ham" :src="IMG_HAM" @click="toggleMenu"/>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import PremiumBar from './PremiumBar.vue';

  import {IMG_LOGO, IMG_HAM} from "../js/images";

  import {NS_UI} from "../store/ui";
  import {ROUTE_APP_IS_REQUIRED} from "../router";

  export default {
    data() {
      return {
        activeRoute: this.$route.path,

        IMG_LOGO,
        IMG_HAM,
      };
    },
    computed: {
      ...mapGetters(NS_UI, ['menuVisible']),

      isMenuVisible() {
        return ![ROUTE_APP_IS_REQUIRED].includes(this.$route.path);
      },

      title() {
        return this.$route.meta.title;
      }
    },
    watch: {
      $route(to, from) {
        this.activeRoute = to;
      }
    },
    methods: {
      ...mapActions(NS_UI, ['setMenuVisible']),

      toggleMenu() {
        this.setMenuVisible(!this.menuVisible);
      },
    },
    components: {
      PremiumBar
    }
  }
</script>

<style lang="scss" scoped>
  .header {

    .menu-bar {
      height: 83px;
      background-color: #F7F7F7;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-left: 24px;
      padding-right: 24px;

      h1{
        white-space: nowrap;
        font-size: 20px;
        text-transform: uppercase;
        font-weight: 800;
        color: #707070;
      }

      .logo {
        width: 131px;
        height: 44px;
      }

      .ham {
        width: 46px;
        height: 46px;
      }
    }
  }
</style>