const MAIN_ICON = chrome.extension.getURL('img/icon128.png');

export const Notifications = {
  showError(msg) {
    chrome.notifications.create(
      'error', {
        type: 'basic',
        iconUrl: MAIN_ICON,
        title: "Tuxler Error",
        message: msg
      });
  },
  showMessage(msg) {
    chrome.notifications.create(
      'info', {
        type: 'basic',
        iconUrl: MAIN_ICON,
        title: "Tuxler",
        message: msg
      });
  }
};