//close step 1 tab
export const closeStep1Tab = () => {

    if(window.tab_info !== false)
    {
        try {
            if (chrome.runtime.lastError) { }
            chrome.tabs.remove(window.tab_info);
            if (chrome.runtime.lastError) { }
        }
        catch (err) { }

        window.tab_info = false;
    }
}
