<template>
  <div class="flex vertical-center">
    <div class="type-switcher">
      <div class="type" :class="{'active': !useResidentialIPs}" @click="setUseResidentialIPs(false)">DATA CENTER</div>
      <div class="type" :class="{'active': useResidentialIPs}" @click="setResidential">RESIDENTIAL</div>
    </div>
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';
  import {NS_SYSTEM} from "../store/system";
  import {Storage} from "../../js/storage";
  import {ROUTE_APP_IS_REQUIRED} from "../router";

  export default {
    computed: {
      ...mapGetters(NS_SYSTEM, ['useResidentialIPs'])
    },
    methods: {
      ...mapActions(NS_SYSTEM, ['setUseResidentialIPs']),

      setResidential() {
        console.log('setResidential(): ', Storage.app.isInstalled());
        if (!Storage.app.isInstalled()) {
          this.$router.push({path: ROUTE_APP_IS_REQUIRED});
          return;
        }
        this.setUseResidentialIPs(true);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .type-switcher {
    width: 100%;
    height: 25px;
    background-color: #dedcdc;
    border-radius: 20px;
    position: relative;
    display: flex;
    margin-top: 30px;

    .type {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      color: white;
      font-weight: 800;
      text-transform: uppercase;
      position: relative;
      transition: all 0.3s ease;
    }

    .type:not(.active) {
      cursor: pointer;
    }

    .type.active {
      background-color: #FFB22A;
      border-radius: 20px;
      position: relative;
    }

    .type::before {
      content: '';
      opacity: 0.3;
      border: 0 solid #FFB22A;
      border-radius: 20px;
      position: absolute;
      height: 100%;
      width: 100%;
      transition: all 0.3s ease;
    }

    .type.active::before {
      border: 7.5px solid #FFB22A;
    }
  }
</style>