<template>
  <Info :white="white">
    <img v-if="icon" :src="icon"/>

    <div class="content" :style="styleContent">
      <slot></slot>
    </div>

    <IconExit @click="onExit"></IconExit>
  </Info>
</template>

<script>
  import Info from '../components/Info.vue';
  import IconExit from '../components/icons/IconExit.vue';

  export default {
    computed: {
      styleContent() {
        return {
          'align-items': this.align
        }
      }
    },
    components: {
      Info,
      IconExit
    },
    methods: {
      onExit() {
        if (this.customExit) {
          this.$emit('onExit');
        } else {
          this.$router.go(-1);
        }
      }
    },
    props: {
      icon: String,
      align: {
        type: String,
        default: 'default'
      },
      white: {
        type: Boolean,
        default: false
      },
      customExit: {
        type: Boolean,
        default: false
      }
    }
  }
</script>

