<template>
  <div class="reload" @click="reload">
    <div class="text">
      <p>RELOAD TO NEXT NEARBY LOCATION</p>
    </div>
    <div class="icon">
      <img class="icon--reload" :src="IMG_RELOAD_ICON"/>
    </div>
  </div>
</template>

<script>
  import {Storage} from "../../../js/storage";
  import Messages from "../../../js/messages";
  import {IMG_RELOAD_ICON} from "../../js/images";

  export default {
    data() {
      return {
        IMG_RELOAD_ICON
      }
    },
    methods: {
      async reload() {
        console.log('~reload~');
        const location = await Storage.local.get(['selectedCountryISO', 'selectedCity']);
        console.log('location: ', location);

        Messages.toBackground().setNextNearby(location.selectedCountryISO, location.selectedCity);
      }
    },
  }
</script>

