<template>
  <div class="custom-select">
    <div v-if="item" class="select-selected" :class="{'select-arrow-active': !selectHidden}" @click="replacerClick">
      <div v-if="item.icon" class="select-item-icon" :style="`background-image: url('${item.icon}')`"></div>
      {{item.text}}
    </div>

    <div ref="container" v-if="!selectHidden" class="select-items" :class="{'select-hide': selectHidden}" :style="styleDropdown">
      <div class="select-item" :class="{'same-as-selected': c.value === val}" v-for="c in items" :key="c.value" @click="itemClick(c)">
        <div v-if="c.icon" class="select-item-icon" :style="`background-image: url('${c.icon}')`"></div>
        {{c.text}}
      </div>
    </div>
  </div>
</template>

<script>

  // Items with icon
  const RICH_ITEM_HEIGHT = 50;
  const RICH_MAX_HEIGHT = 250;

  // Text only
  const ITEM_HEIGHT = 36;
  const MAX_HEIGHT = 180;

  var GeminiScrollbar = require('gemini-scrollbar')

  let gs;

  export default {
    data() {
      return {
        val: null,
        ignoreClick: false,
        selectHidden: true
      };
    },
    computed: {
      item() {
        // return this.items.find(i => i.value === this.val) || this.items.find(i => i.value.toLowerCase() === 'any');
        return this.items.find(i => i.value === this.val) || this.items[0];
      },
      hasIcons() {
        return this.items.find(i => 'icon' in i);
      },
      styleDropdown() {
        const maxHeight  = this.hasIcons ? RICH_MAX_HEIGHT : MAX_HEIGHT;
        const itemHeight  = this.hasIcons ? RICH_ITEM_HEIGHT : ITEM_HEIGHT;

        const height = Math.min(maxHeight, this.items.length * itemHeight);

        return {height: `${height}px`};
      }
    },
    watch: {
      selectHidden(isHidden) {
        console.log('watch@selectHidden: ', isHidden);
        if (!isHidden) {
          this.$nextTick(() => {
            if (gs) {
              gs.destroy();
            }

            gs = new GeminiScrollbar({
              element: this.$refs.container,
              forceGemini: true
            }).create();


            /*
             * By default closeSelect() is hidhing the popup when a user is pressing mouse down at the vertcal scroll
             * thumb and trying to scroll the list. The next code is used to prevent popup closing for this case.
             */
            const thumb = this.$refs.container.querySelector('.gm-scrollbar .thumb');
            thumb.addEventListener('mousedown', e => {
              this.ignoreClick = true;
              document.addEventListener('mouseup', e => {
                setTimeout(() => {
                  this.ignoreClick = false;
                }, 50);
              }, {once: true});
            }, {once: true});
          });
        }
      },
      value(newVal) {
        console.log('@@@@@@@@@@@ watch value: ', newVal);
        this.val = newVal;
      }
    },
    mounted() {
      this.val = this.value;
      console.log('ITEMS: ', JSON.stringify(this.items));

      document.addEventListener("click", this.closeSelect);
    },
    beforeDestroy() {
      document.removeEventListener("click", this.closeSelect);

      if (gs) {
        gs.destroy();
        gs = null;
      }
    },
    methods: {
      closeSelect(e) {
        console.log('~closeSelect~', e);
        console.log('this.ignoreClick: ', this.ignoreClick);

        if (this.ignoreClick) {
          this.ignoreClick = false;
          return;
        }

        this.selectHidden = true;
      },
      replacerClick() {
        this.$emit('beforeShow');

        this.ignoreClick = true;
        this.selectHidden = !this.selectHidden;
      },
      itemClick(item) {
        this.val = item.value;
        this.closeSelect();
        this.$emit('click', item);
      }
    },
    props: ['items', 'value']
  }
</script>

<style lang="scss" scoped>
  .custom-select {
    position: relative;
    width: 100%;
    background-color: transparent;
    border-left: none;
    border-top: none;
    border-right: none;
    border-color: #c5c1c1bf;
    text-align: center;
    outline: none;
    padding: 5px;
    font-weight: 800;
    font-size: 14px;
    color: #c5c1c1bf;
  }

  .select-selected {
    font-weight: 800;
    font-size: 14px;
    color: #c5c1c1bf;
    user-select: none;
  }

  .select-selected:after {
    position: absolute;
    content: "";
    top: 25px;
    right: 10px;
    width: 0;
    height: 0;
    border: 6px solid;
    border-color: #c5c1c1bf transparent transparent transparent;
  }

  .select-selected.select-arrow-active:after {
    border-color: transparent transparent #c5c1c1bf transparent;
    top: 18px;
  }

  .select-item, .select-selected {
    display: flex;
    align-items: center;
    padding: 8px 16px;
    border: 1px solid;
    border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
    cursor: pointer;
    transition: all 0.3s ease;

    user-select: none;
  }

  .select-items {
    position: absolute;
    background-color: white;
    top: 86%;
    left: 0;
    right: 0;
    z-index: 99;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    font-size: 13px;

    max-height: 250px;

    /* because of custom scroll */
    overflow: hidden;
  }

  .select-item-icon {
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    margin-right: 8px;
  }

  .select-hide {
    display: none;
  }

  .select-items > div:hover, .same-as-selected {
    color: white;
    background-color: rgba(255, 178, 42, 1);
  }
</style>