<template>
  <div class="bottom-bar" :class="protected ? 'state-on' : 'state-off'">
    <img class="icon--flag" :src="icon"/>
    <p v-if="location" class="block-location">
      {{location.country}}
      <br>
      {{regionAndCity}}
      <br>
      <span v-if="protected" class="time-ago">({{changedLabel}})</span>
    </p>


    <img v-if="protected" class="icon--protected-mini" :src="IMG_PROTECTED_MINI_ICON"/>
    <img v-else class="icon--unprotected-mini" :src="IMG_UNPROTECTED_MINI_ICON"/>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from 'vuex';
  import {NS_SYSTEM} from "../store/system";

  import Messages from "../../js/messages";

  import {
    IMG_PROTECTED_MINI_ICON,
    IMG_UNPROTECTED_MINI_ICON
  } from "../js/images";
  import {TimeUtils} from "../js/utils";
  import {Storage} from "../../js/storage";

  export default {
    data() {
      return {
        icon: '',

        IMG_PROTECTED_MINI_ICON,
        IMG_UNPROTECTED_MINI_ICON
      };
    },
    computed: {
      ...mapGetters(['location', 'lastTimeChanged']),

      ...mapGetters(NS_SYSTEM, ['protected']),

      regionAndCity() {
        return this.location.region === 'unknown' ? this.location.city : `${this.location.region} / ${this.location.city}`;
      },

      locationISO() {
        return this.location ? this.location.countryiso : '';
      },
      changedLabel() {
        if (this.lastTimeChanged) {
          return TimeUtils.getLocationChangedTime(this.lastTimeChanged) + " " + chrome.i18n.getMessage("main_ago");
        }

        return chrome.i18n.getMessage("main_never");
      },

    },
    watch: {
      async locationISO(newISO) {
        this.icon = await Messages.toBackground().getFlagIcon(newISO, true);
      }
    },
    async mounted() {
      console.log('footer ~mounted~');
      console.log('this.location:', this.location);
      console.log('this.locationISO:', this.locationISO);

      if (this.location) {
        this.icon = await Messages.toBackground().getFlagIcon(this.locationISO, true);
      }
    }
  }
</script>

