      .block-location {
        position:absolute;
        left:128px;
      }

      .icon--protected-mini {
        position:absolute;
        right:0px;
        top: 5px;
      }
    }

    &.state-off {
      position:relative;

      .icon--flag {
        position: absolute;
        left:10px;
      }

      .block-location {
        position:absolute;
        left:128px;
      }

      .icon--unprotected-mini {
        position:absolute;
