<template>
  <InfoPage align="center" white="true" :icon="IMG_INFO_ICON">

    <p class="mt-large mb-xl">{{msg}}</p>

    <a class="btn main gold" @click="goBack">RETURN</a>
  </InfoPage>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex';

  import {_} from '../js/i18n';

  import InfoPage from "../components/InfoPage.vue";

  import {ROUTE_DASHBOARD} from "../router";

  import {IMG_INFO_ICON} from "../js/images";
  import {NS_UI} from "../store/ui";

  // TODO: do it
  // var message = chrome.i18n.getMessage("message_limit_free");
  // message = message.split("\\n").join("\n");

  export default {
    data() {
      return {
        msg: '',

        IMG_INFO_ICON
      };
    },
    computed: {
      ...mapGetters(NS_UI, ['message']),
    },
    mounted() {
      this.msg = _(this.message) || this.message;
      this.setMessage(null);
    },
    methods: {
      goBack() {
        this.$router.replace({path: ROUTE_DASHBOARD});
      }
    },
    components: {
      InfoPage
    }
  }
</script>