<template>
  <InfoPage align="center" white="true">
    <h1>
      HOW DO YOU LIKE<br>
      TUXLER VPN?
    </h1>
    <div class="stars" @mouseleave="leaveStars">
      <div class="star" :class="{'color': hasColor(1)}" @click="selectStar(1)" @mouseenter="overStar(1)"
           @mouseleave="leaveStar"></div>
      <div class="star" :class="{'color': hasColor(2)}" @click="selectStar(2)" @mouseenter="overStar(2)"
           @mouseleave="leaveStar"></div>
      <div class="star" :class="{'color': hasColor(3)}" @click="selectStar(3)" @mouseenter="overStar(3)"
           @mouseleave="leaveStar"></div>
      <div class="star" :class="{'color': hasColor(4)}" @click="selectStar(4)" @mouseenter="overStar(4)"
           @mouseleave="leaveStar"></div>
      <div class="star" :class="{'color': hasColor(5)}" @click="selectStar(5)" @mouseenter="overStar(5)"
           @mouseleave="leaveStar"></div>
    </div>
    <a class="btn main gold" @click="submitRating">SUBMIT</a>
  </InfoPage>
</template>

<script>
  //TODO: disable Submit button on start, enable after selecting rating.
  import Messages from "../../js/messages";
  import {Storage} from "../../js/storage";

  import {ROUTE_FEEDBACK, ROUTE_SHARE_RATING} from "../router";

  import InfoPage from "../components/InfoPage.vue";

  export default {
    data() {
      return {
        selectedIndex: 0,
        activeIndex: 0
      }
    },
    methods: {
      selectStar(idx) {
        this.selectedIndex = idx;
      },
      overStar(idx) {
        this.activeIndex = idx;
      },
      leaveStar(evt) {
        console.log('out evt');
        console.log(evt);

        const toElementClass = evt.toElement ? evt.toElement.className : null;

        if (['star', 'stars'].includes(toElementClass)) {
          return;
        }

        this.activeIndex = 0;
      },
      leaveStars(evt) {
        console.log('leaveStars out evt');
        console.log(evt);

        const toElementClass = evt.toElement ? evt.toElement.className : null;

        if (['star'].includes(toElementClass)) {
          return;
        }

        this.activeIndex = 0;
      },
      hasColor(idx) {
        return idx <= Math.max(this.activeIndex, this.selectedIndex);
      },

      submitRating() {
        Messages.toApi().submitRating(this.selectedIndex);
        Storage.pages.setPage(null);

        if (this.selectedIndex < 4) {
          this.$router.replace({path: ROUTE_FEEDBACK});
        } else {
          this.$router.replace({path: ROUTE_SHARE_RATING});
        }
      }
    },
    components: {
      InfoPage
    }
  }
</script>

<style lang="scss" scoped>
  .stars {
    align-self: center;

    display: flex;
    align-items: center;
    justify-content: center;
    margin: 25px 0 50px 0;

    .star {
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      background-image: url('/popup/img/star.png');
      cursor: pointer;
      height: 25px;
      width: 25px;
      margin: 0 1px;

      &.color, &:hover {
        background-image: url('/popup/img/star-color.png');
      }
    }
  }
</style>